import { TelegramClient } from 'telegram';
import { StringSession } from 'telegram/sessions';
import { debug } from '@actions/core';
const stringSession = '';
export class Telegram {
    _client;
    constructor(apiId, apiHash) {
        this._client = new TelegramClient(new StringSession(stringSession), apiId, apiHash, { connectionRetries: 5 });
    }
    get client() {
        return this._client;
    }
    async setup(botToken) {
        debug('Initiating telegram connection.');
        await this._client.start({
            botAuthToken: botToken
        });
        this._client.session.save();
    }
    async terminate() {
        this._client.session.delete();
        await this._client.disconnect();
        await this._client.destroy();
        debug('Terminating connection.');
    }
}
export const uploadReleaseAsset = async (client, chatId, file) => {
    return client.sendFile(chatId, {
        file
    });
};
